























































import uniPopup from '../../components/uni-popup.vue';
export default {
	data() {
		return {
			popType: 'middle',
			title: '确定要退出当前用户吗',
			showPopupMiddle: false,
		}
	},
	onLoad() {
		var token =(uni.getStorageSync('user_info')).token;
		console.log(token)
		uni.request({
			url:'http://bus.liebianzhe.com/api/user/get_user_data',
			data:{
				token:token
			},
			success: (res) => {
				console.log(res)
			}
		})
	},
	methods:{
		hidePopup: function() {
			this.showPopupMiddle = false;
		},
		quexiao(){
			this.showPopupMiddle = false;
		},
		clear_stor(){
			try {
				uni.clearStorageSync();
				uni.showToast({
					title:'已清空缓存'
				})
			} catch (e) {
				error
			}
		},
		queing(){
			var token =(uni.getStorageSync('user_info')).token;
			
			uni.request({
				url:'http://bus.liebianzhe.com/api/login/logout',
				data:{
					token:token
				},
				success: (res) => {
					console.log(res)
					if(res.data.code==1){
						uni.navigateTo({
							url:'../login/login'
						})
					}else{
						uni.showToast({
							title:'退出失败'
						})
					}
				}
			})
		},
		showMiddlePopup: function() {
			this.hidePopup();
			this.popType = 'middle';
			this.showPopupMiddle = true;
		},
		jump_phone(){
			uni.navigateTo({
				url:'../shezhi/change_phone'
			})
		},
		jump_ch_password(){
			uni.navigateTo({
				url:'../shezhi/modifer_password'
			})
		},
		jump_pay(){
			uni.navigateTo({
				url:'../shezhi/modifer_pay'
			})
		},
		jump_name(){
			uni.navigateTo({
				url:'../shezhi/modifer_name'
			})
		},
		jump_info(){
			uni.navigateTo({
				url:'../info/info'
			})
		},
		login_out(){
			uni.reLaunch({
				url:'../login/login'
			})
		}
	},
	components: {
		uniPopup
	}
}
